/* Waves Animation */
.footer .waves {
  position: absolute;
  top: -80px;
  left: 0px;
  width: 100%;
  height: 100px;
  background: url(data:image/png;base64,);
  -webkit-filter: hue-rotate(280deg);
          filter: hue-rotate(280deg);
  background-size: 1000px 100px;
  -webkit-animation: waveMotion 4s linear infinite;
          animation: waveMotion 4s linear infinite;
}
.waves #wave1 {
  position: absolute;
  left: 0px;
  width: 100%;
  height: 100px;
  opacity: 0.6;
  background: url(data:image/png;base64,);
  background-size: 1000px 100px;
  z-index: 99;
  -webkit-animation: waveMotion1 4s linear infinite;
          animation: waveMotion1 4s linear infinite;
}
.waves #wave2 {
  position: absolute;
  left: 0px;
  width: 100%;
  height: 100px;
  opacity: 0.5;
  background: url(data:image/png;base64,);
  background-size: 1000px 100px;
  z-index: 98;
  -webkit-animation: waveMotion2 3s linear infinite;
          animation: waveMotion2 3s linear infinite;
}
.waves #wave3 {
  position: absolute;
  left: 0px;
  width: 100%;
  height: 100px;
  opacity: 0.2;
  background: url(data:image/png;base64,);
  background-size: 1000px 100px;
  z-index: 97;
  -webkit-animation: waveMotion3 4s linear infinite;
          animation: waveMotion3 4s linear infinite;
}
.waves #wave4 {
  position: absolute;
  left: 0px;
  width: 100%;
  height: 100px;
  opacity: 0.7;
  background: url(data:image/png;base64,);
  background-size: 1000px 100px;
  z-index: 96;
  -webkit-animation: waveMotion 5s linear infinite;
          animation: waveMotion 5s linear infinite;
}
/*    Animation for Waves    */
@-webkit-keyframes waveMotion {
  0% {
    background-position-x: 1000px;
  }
}
@keyframes waveMotion {
  0% {
    background-position-x: 1000px;
  }
}
@-webkit-keyframes waveMotion1 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}
@keyframes waveMotion1 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}
@-webkit-keyframes waveMotion2 {
  0% {
    background-position-x: 1000px;
  }
}
@keyframes waveMotion2 {
  0% {
    background-position-x: 1000px;
  }
}
@-webkit-keyframes waveMotion3 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}
@keyframes waveMotion3 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}

