  gap: 50px;
}
.regis-form h1 {
  text-align: center;
  letter-spacing: 2px;
  color: #ffffff;
  text-shadow: 2px 2px 5px rgba(61, 59, 59, 0.5);
}
.registration form {
  background-color: rgba(255, 255, 255, 0.3);
  border-top: 2px solid rgba(255, 255, 255, 0.3);
  border-left: 2px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0px 20px 45px 5px rgba(255, 255, 255, 0.295),
    10px 25px 25px rgba(0, 0, 0, 0.164);
  border-radius: 7px;
  padding: 65px 50px;
}
.registration .inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-around;
  grid-gap: 50px;
  gap: 50px;
  grid-column-gap: 70px;
  -webkit-column-gap: 70px;
          column-gap: 70px;
}
.registration .input {
  font-size: 20px;
  position: relative;
  height: 30px;
  width: 100%;
  border-bottom: 2px dashed rgb(18, 18, 19);
}
.registration .input::after {
  content: "";
  position: relative;
  display: block;
  height: 4px;
  width: 100%;
  background: #2a2a2b;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: 0%;
          transform-origin: 0%;
  opacity: 0;
  transition: all 500ms ease;
  bottom: -28px;
}

.registration .input:focus-within::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  opacity: 1;
}
.registration form .inputs input {
  border: none;
  outline: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  color: rgb(20, 19, 19);
  font-size: 17px;
}
.registration .input label {
  position: absolute;
  color: white;
  transition: 0.6s;
}
.registration .input label span {
  color: red;
}
.registration .input:focus-within label,
.registration input:not(:placeholder-shown) + label {
  -webkit-transform: translate(-8px, -20px) scale(0.9);
          transform: translate(-8px, -20px) scale(0.9);
  color: rgb(83, 80, 80);
}

