/* Submit Button */
.submit {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  position: relative;
  top: 15px;
  margin-top: 35px;
}
.submit input {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 20px;
  padding: 10px 20px;
  border-radius: 7px;
  background-color: #fd9644;
  color: white;
  box-shadow: 0px 0px 0px 3px white;
  border: none;
}
.submit p {
  align-self: flex-start;
  font-size: 0.25rem;
}
.submit p span {
  color: red;
}
/* Validate */
.validate {
  position: absolute;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  left: 0px;
  top: 75px;
  opacity: 0;
  z-index: -1;
  font-size: 15px;
  background-color: #485460;
  padding: 10px;
  color: white;
  border-radius: 7px;
  box-shadow: 0px 0px 0px 4px #8699ac;
  transition: 0.6s;
}
.registration .input:focus-within .validate {
  opacity: 1;
  top: 55px;
  z-index: 3;
}
.validate::after {
  content: " ";
  width: 22px;
  height: 22px;
  position: absolute;
  top: -15px;
  left: 20px;
  border-left: 4px solid #8699ac;
  border-top: 4px solid #8699ac;
  background-color: #485460;
  z-index: -11;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.validate li {
  list-style: none;
}

