.up-events ul li {
  position: relative;
  list-style: none;
  /*  Adjust these   */
  padding-left: 0.4rem;
  padding-right: 0.05rem;
  padding-top: 0.03rem;
  margin: 0.1rem 0rem;
  color: white;
  z-index: 5;
  cursor: pointer;
}
.up-events li::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 30px;
  background-color: transparent;
  border: 2px solid white;
  transition: 0.7s;
  transition-delay: 0.2s;
}
.up-events li::after {
  content: "";
  position: absolute;
  top: 0.11rem;
  left: 0.09rem;
  width: 0.1rem;
  height: 0.1rem;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-top: 2px solid white;
  border-right: 2px solid white;
  transition: 0.4s;
  transition-delay: 0.2s;
}
.up-events ul li:hover::before {
  width: 100%;
  border-radius: 16px;
  background-color: #55efc4;
  z-index: -5;
}
.up-events ul li:hover::after {
  top: 0.11rem;
  left: 0.13rem;
  -webkit-transform: scale(1.3) rotate(45deg);
          transform: scale(1.3) rotate(45deg);
}
.up-events .blur {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: #45aaf2;
  pointer-events: none;
  -webkit-user-select: none;
          user-select: none;
