/* Login */
.login,
.update-event {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.3);
  padding: 0.4rem 0.5rem;
  border-left: solid 2px rgba(255, 255, 255, 0.5);
  border-top: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 15px;
}
.login h2,
.update-event h2 {
  font-size: 50px;
  color: rgb(82, 76, 76);
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.login form,
.update-event form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login form [type="submit"],
.update-event form [type="submit"] {
  width: fit-content;
  padding: 5px 10px;
  background-color: #e1b12c;
  box-shadow: 0px 0px 0px 3px #dbcc81e1;
  border-radius: 5px;
  font-size: 20px;
  border: none;
  cursor: pointer;
}
.login form input,
.update-event input {
  outline: none;
  background: transparent;
  color: white;
  border: none;
  margin: 15px 0px;
  border-bottom: 3px solid #7f8fa6;
  font-size: 18px;
  padding: 5px 7px;
  transition: 0.5s;
  width: 100%;
}
.login form input:focus,
.update-event form input:focus {
  border-bottom: 4px solid #f0932b;
}
.login form input:nth-child(3):focus,
.update-event form input:nth-child(5) {
  border-bottom: none;
  font-size: 23px;
}
/*  Update Event  */
.update-event {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 0.4rem 0.5rem;
  border-left: solid 2px rgba(255, 255, 255, 0.5);
  border-top: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 15px;
}
.update-event [type="file"] {
  width: inherit;
}
